
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    progress: {
      type: Number,
      required: true,
    },
    label: String,
  },
  computed: {
    actualProgress() {
      if (this.progress > 100) {
        return 100;
      }

      if (this.progress < 0) {
        return 0;
      }

      return this.progress;
    },
  },
});
